// todo -------------------------- Classes -------------------------------------
// #region
.wrapper {
	@extend .gradient;
	min-height: 100%;
	margin: 0 auto;
}

[class*='_container'] {
	/* border: groove; */
	max-width: $maxWidthContainer;
	box-sizing: content-box;
	margin: 0 auto;
	padding: 0 15px;
}

[class*='grid-'] {
	display: grid;
}

[class*='icon-'] {
	cursor: pointer;
	height: 100%;
	@include flex-align-center-justify-center;
}

[class*='_stretch'] {
	position: relative;
	z-index: 0;
	// min-height: calc(100vh - ($headerHeight + $footerHeight));
}

